import React from "react";
import { Link } from "gatsby";

import "./ExamplePanel.css";

import pipeline from "../../../static/example1.svg";

const ExamplePanel = ({ frontmatter, html, excerpt }) => {
  const { path, title } = frontmatter;

  return (
    <div className="link">
      <Link to={path}>
        <img className="image-bg" src={pipeline} alt="" />
      </Link>
      <h3>
        <Link to={path}>{title}</Link>
      </h3>
      <div>{path}</div>
      <div dangerouslySetInnerHTML={{ __html: html }} />
    </div>
  );
};

export default ExamplePanel;
